/* AI 사주팔자 페이지 전용 스타일 */

.saju-page .page-header {
  background: linear-gradient(135deg, rgba(139, 69, 19, 0.05) 0%, rgba(218, 165, 32, 0.05) 100%);
  padding: 80px 0 60px;
  text-align: center;
}

.saju-page .page-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 16px;
  color: var(--gray-900);
  text-align: center;
}

.saju-page .page-subtitle {
  font-size: 1.125rem;
  color: var(--gray-600);
  text-align: center;
}

.saju-section {
  padding: 60px 0;
  background: white;
}

.saju-form-container {
  max-width: 600px;
  margin: 0 auto;
  background: white;
  padding: 40px;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.saju-form-container h2 {
  text-align: center;
  margin-bottom: 30px;
  color: var(--gray-900);
}

.form-row {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  gap: 15px;
}

.form-group {
  flex: 1;
  min-width: 150px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: var(--gray-700);
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid var(--gray-300);
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.btn-analyze {
  width: 100%;
  background: linear-gradient(135deg, #8b4513 0%, #daa520 100%);
  color: white;
  padding: 16px 24px;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 20px;
}

.btn-analyze:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(139, 69, 19, 0.3);
}

.btn-analyze:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

#loading {
  text-align: center;
  padding: 40px;
  background: white;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  margin-top: 30px;
}

.loading-spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #8b4513;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

#result {
  background: white;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  margin-top: 30px;
  overflow: hidden;
}

.result-header {
  background: linear-gradient(135deg, #8b4513 0%, #daa520 100%);
  color: white;
  padding: 30px;
  text-align: center;
}

.result-header h2 {
  margin: 0 0 10px 0;
  font-size: 1.8rem;
}

.result-header p {
  margin: 0;
  opacity: 0.9;
  font-size: 1.1rem;
}

.result-content {
  padding: 40px;
}

.saju-section-block {
  margin-bottom: 30px;
  padding: 25px;
  background: #f8f9fa;
  border-radius: 12px;
  border-left: 4px solid #8b4513;
}

.saju-section-block h3 {
  color: #8b4513;
  margin-bottom: 15px;
  font-size: 1.3rem;
}

.saju-section-block p {
  line-height: 1.7;
  color: var(--gray-700);
  margin-bottom: 10px;
}

.saju-section-block:last-child {
  margin-bottom: 0;
}

/* 반응형 스타일 */
@media (max-width: 768px) {
  .saju-page .page-title {
    font-size: 2rem;
  }

  .saju-form-container {
    padding: 25px 20px;
    margin: 0 15px;
  }

  .form-row {
    flex-direction: column;
  }

  .form-group {
    min-width: auto;
  }

  .result-content {
    padding: 25px 20px;
  }

  .saju-section-block {
    padding: 20px 15px;
  }
}

@media (max-width: 480px) {
  .saju-page .page-header {
    padding: 60px 0 40px;
  }

  .saju-page .page-title {
    font-size: 1.8rem;
  }

  .saju-form-container {
    margin: 0 10px;
    padding: 20px 15px;
  }

  .btn-analyze {
    font-size: 16px;
    padding: 14px 20px;
  }
}
