/* 연봉 계산기 페이지 전용 스타일 */

.salary-calculator-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}

.calculator-header {
  text-align: center;
  margin-bottom: 3rem;
}

.calculator-header h1 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--gray-900);
  margin-bottom: 1rem;
}

.calculator-header p {
  font-size: 1.2rem;
  color: var(--gray-600);
  line-height: 1.6;
}

.calculator-form {
  background: white;
  padding: 2.5rem;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  font-weight: 600;
  color: var(--gray-700);
  margin-bottom: 0.5rem;
  font-size: 1rem;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid var(--gray-200);
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.form-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.calculate-btn {
  width: 100%;
  background: var(--gradient-primary);
  color: white;
  padding: 16px 24px;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

.calculate-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.3);
}

.result-container {
  display: none;
  background: white;
  padding: 2.5rem;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.result-container.show {
  display: block;
}

.result-header {
  text-align: center;
  margin-bottom: 2rem;
}

.result-header h2 {
  font-size: 1.8rem;
  color: var(--gray-900);
  margin-bottom: 0.5rem;
}

.result-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.result-item {
  background: var(--gray-50);
  padding: 1.5rem;
  border-radius: 12px;
  text-align: center;
  border: 2px solid var(--gray-200);
  transition: all 0.3s ease;
}

.result-item:hover {
  border-color: var(--primary-color);
  transform: translateY(-3px);
}

.result-label {
  font-size: 0.9rem;
  color: var(--gray-600);
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.result-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary-color);
}

.result-value.highlight {
  font-size: 2rem;
  color: var(--success-color);
}

.deduction-details {
  background: var(--gray-50);
  padding: 1.5rem;
  border-radius: 12px;
  margin-top: 1rem;
}

.deduction-details h3 {
  font-size: 1.2rem;
  color: var(--gray-900);
  margin-bottom: 1rem;
}

.deduction-list {
  list-style: none;
  padding: 0;
}

.deduction-list li {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
  border-bottom: 1px solid var(--gray-200);
}

.deduction-list li:last-child {
  border-bottom: none;
  font-weight: 600;
  color: var(--gray-900);
}

/* 추가 스타일 */
.year-badge {
  background: var(--success-color);
  color: white;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  margin-left: 8px;
  vertical-align: middle;
}

.input-wrapper {
  position: relative;
}

.input-unit {
  position: absolute;
  right: 16px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--gray-500);
  font-weight: 500;
  pointer-events: none;
}

.result-main {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.result-item.highlight {
  background: var(--gradient-primary);
  color: white;
  transform: scale(1.05);
}

.result-item.highlight .result-label,
.result-item.highlight .result-detail {
  color: rgba(255, 255, 255, 0.9);
}

.result-item.highlight .result-value {
  color: white;
}

.result-detail {
  font-size: 0.85rem;
  color: var(--gray-500);
  margin-top: 0.25rem;
}

.deduction-title {
  font-size: 1.3rem;
  color: var(--gray-900);
  margin-bottom: 1.5rem;
  font-weight: 600;
}

.deduction-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.deduction-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: white;
  border-radius: 8px;
  border: 1px solid var(--gray-200);
}

.deduction-item-name {
  font-size: 0.95rem;
  color: var(--gray-700);
  font-weight: 500;
}

.deduction-item-value {
  font-size: 1.1rem;
  color: var(--gray-900);
  font-weight: 600;
}

.info-box {
  background: #f0f9ff;
  border: 1px solid #bae6fd;
  border-radius: 12px;
  padding: 1.5rem;
  margin-top: 2rem;
}

.info-box h3 {
  color: #0369a1;
  font-size: 1.1rem;
  margin-bottom: 0.75rem;
}

.info-box p {
  color: #0c4a6e;
  line-height: 1.6;
  font-size: 0.95rem;
}

/* 반응형 스타일 */
@media (max-width: 768px) {
  .salary-calculator-container {
    padding: 1rem;
  }

  .calculator-header h1 {
    font-size: 2rem;
  }

  .calculator-header p {
    font-size: 1.1rem;
  }

  .calculator-form,
  .result-container {
    padding: 2rem 1.5rem;
  }

  .form-row {
    grid-template-columns: 1fr;
  }

  .result-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .result-item {
    padding: 1.2rem;
  }

  .result-value.highlight {
    font-size: 1.8rem;
  }
}

@media (max-width: 480px) {
  .calculator-header h1 {
    font-size: 1.8rem;
  }

  .calculator-form,
  .result-container {
    padding: 1.5rem 1rem;
  }

  .calculate-btn {
    font-size: 16px;
    padding: 14px 20px;
  }

  .result-value {
    font-size: 1.3rem;
  }

  .result-value.highlight {
    font-size: 1.6rem;
  }
}
