/* Legal pages specific styles */

/* Hero section for legal pages */
.legal-hero {
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
  color: white;
  padding: 100px 20px 80px;
  text-align: center;
  margin-bottom: 0;
}

.legal-hero-content h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: white;
}

.legal-hero-content p {
  font-size: 1.2rem;
  opacity: 0.9;
  max-width: 600px;
  margin: 0 auto;
  color: white;
}

.legal-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 4rem 1rem;
  min-height: 100vh;
}

.legal-container h1 {
  font-size: 2.5rem;
  color: var(--gray-900);
  margin-bottom: 1rem;
  text-align: center;
}

.legal-container h2 {
  font-size: 1.75rem;
  color: var(--gray-800);
  margin-top: 3rem;
  margin-bottom: 1rem;
  border-bottom: 2px solid var(--gray-200);
  padding-bottom: 0.5rem;
}

.legal-container h3 {
  font-size: 1.25rem;
  color: var(--gray-700);
  margin-top: 2rem;
  margin-bottom: 1rem;
}

.legal-container p {
  font-size: 1rem;
  line-height: 1.8;
  color: var(--gray-700);
  margin-bottom: 1.5rem;
}

.legal-container ul,
.legal-container ol {
  margin-bottom: 1.5rem;
  padding-left: 2rem;
}

.legal-container li {
  font-size: 1rem;
  line-height: 1.8;
  color: var(--gray-700);
  margin-bottom: 0.5rem;
}

.legal-container table {
  width: 100%;
  border-collapse: collapse;
  margin: 2rem 0;
}

.legal-container th,
.legal-container td {
  padding: 0.75rem;
  text-align: left;
  border: 1px solid var(--gray-300);
}

.legal-container th {
  background-color: var(--gray-50);
  font-weight: 600;
  color: var(--gray-800);
}

.legal-container .update-date {
  text-align: center;
  color: var(--gray-500);
  font-size: 0.875rem;
  margin-bottom: 3rem;
}

.legal-container .section {
  background-color: var(--gray-50);
  padding: 2rem;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
}

.legal-container strong {
  color: var(--gray-900);
  font-weight: 600;
}

/* Legal content wrapper */
.legal-content {
  max-width: 900px;
  margin: 0 auto;
  padding: 60px 20px;
}

/* Legal card container */
.legal-card {
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 40px;
  margin-bottom: 30px;
}

.legal-card h2 {
  font-size: 1.75rem;
  color: #333;
  margin-top: 2rem;
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #e9ecef;
}

.legal-card h3 {
  font-size: 1.25rem;
  color: #555;
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
}

.legal-card p {
  font-size: 1rem;
  line-height: 1.8;
  color: #666;
  margin-bottom: 1.5rem;
}

.legal-card ul,
.legal-card ol {
  margin-bottom: 1.5rem;
  padding-left: 2rem;
}

.legal-card li {
  font-size: 1rem;
  line-height: 1.8;
  color: #666;
  margin-bottom: 0.5rem;
}

/* Table of contents */
.legal-toc {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 30px;
  margin-bottom: 40px;
}

.legal-toc h2 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 2px solid #e9ecef;
}

.legal-toc ul {
  list-style: none;
  padding: 0;
}

.legal-toc li {
  margin-bottom: 12px;
}

.legal-toc a {
  color: #007bff;
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.3s ease;
}

.legal-toc a:hover {
  color: #0056b3;
  text-decoration: underline;
}

/* Info box */
.legal-info-box {
  background: #e7f3ff;
  border-left: 4px solid #007bff;
  padding: 20px;
  margin: 30px 0;
  border-radius: 4px;
}

.legal-info-box p {
  margin: 0;
  color: #004085;
  font-size: 1rem;
}

/* Contact section */
.legal-contact {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 30px;
  text-align: center;
  margin-top: 40px;
}

.legal-contact h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 15px;
}

.legal-contact p {
  color: #666;
  margin-bottom: 25px;
}

.legal-contact-btn {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 12px 30px;
  background: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 6px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.legal-contact-btn:hover {
  background: #0056b3;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(0, 123, 255, 0.3);
}

/* Updated date */
.legal-updated {
  text-align: right;
  color: #666;
  font-size: 0.875rem;
  margin-top: 40px;
  padding-top: 20px;
  border-top: 1px solid #e9ecef;
}

/* Mobile responsive */
@media (max-width: 768px) {
  .legal-container {
    padding: 2rem 1rem;
  }

  .legal-container h1 {
    font-size: 2rem;
  }

  .legal-container h2 {
    font-size: 1.5rem;
  }

  .legal-container h3 {
    font-size: 1.125rem;
  }

  .legal-container table {
    font-size: 0.875rem;
  }

  .legal-container th,
  .legal-container td {
    padding: 0.5rem;
  }

  /* Legal content mobile */
  .legal-content {
    padding: 30px 15px;
  }

  .legal-card {
    padding: 25px 20px;
    margin-bottom: 20px;
  }

  .legal-toc {
    padding: 20px;
    margin-bottom: 30px;
  }

  .legal-hero-content h1 {
    font-size: 2rem;
  }

  .legal-hero-content p {
    font-size: 1rem;
  }

  .legal-contact {
    padding: 20px;
  }

  .legal-contact-btn {
    padding: 10px 24px;
    font-size: 0.95rem;
  }
}
