/* =========================================================================
   Footer Styles
   ========================================================================= */

/* 구체적인 클래스 선택자로 CSS 충돌 방지 - !important 최소화 */
footer.footer {
  background-color: var(--color-gray-900);
  color: var(--color-gray-300);
  padding-top: var(--spacing-16);
  padding-bottom: var(--spacing-8);
  margin-top: auto;
  border-top: none;
  position: relative;
  width: 100%;
}

/* Remove duplicate - now handled by .footer-content */

/* Footer Content Container - 5단 구조 최적화 */
.footer-content {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 1fr; /* 브랜드 섹션을 더 넓게, 5개 섹션 지원 */
  gap: var(--spacing-6);
  margin-bottom: var(--spacing-12);
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 var(--spacing-6);
  align-items: start;
}

/* 큰 태블릿에서는 3단 구조 */
@media (max-width: 1200px) and (min-width: 901px) {
  .footer-content {
    grid-template-columns: 2fr 1fr 1fr;
    gap: var(--spacing-6);
  }
}

/* 작은 태블릿에서는 2x3 구조 */
@media (max-width: 900px) and (min-width: 769px) {
  .footer-content {
    grid-template-columns: 1fr 1fr;
    gap: var(--spacing-6);
  }
}

/* Footer Section */
.footer-section h3 {
  color: var(--color-white);
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  margin-bottom: var(--spacing-4);
  word-break: keep-all;
}

.footer-section p {
  line-height: var(--line-height-relaxed);
  margin-bottom: var(--spacing-4);
  word-break: keep-all;
}

/* 브랜드 섹션 특별 스타일 */
.footer-brand h3 {
  font-size: var(--font-size-xl);
  margin-bottom: var(--spacing-3);
}

.footer-description {
  color: var(--color-gray-400);
  line-height: 1.6;
  margin-bottom: var(--spacing-5);
}

/* 각 섹션별 아이콘과 텍스트 정렬 */
.footer-links a {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
}

/* Footer Links */
.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: var(--spacing-2);
}

.footer-links a {
  color: var(--color-gray-300);
  text-decoration: none;
  transition: color var(--transition-base);
}

.footer-links a:hover {
  color: var(--color-white);
}

/* Social Links */
.footer-social {
  display: flex;
  gap: var(--spacing-3);
  margin-top: var(--spacing-4);
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: var(--color-gray-800);
  border-radius: var(--radius-full);
  color: var(--color-gray-300);
  text-decoration: none;
  transition: all var(--transition-base);
}

.social-link:hover {
  background-color: var(--color-primary);
  color: var(--color-white);
  transform: translateY(-2px);
}

/* Footer Bottom */
.footer-bottom {
  padding-top: var(--spacing-8);
  border-top: 1px solid var(--color-gray-800);
  text-align: center;
}

.footer-copyright {
  font-size: var(--font-size-sm);
  color: var(--color-gray-400);
  margin-bottom: var(--spacing-2);
}

.footer-legal {
  display: flex;
  justify-content: center;
  gap: var(--spacing-4);
  flex-wrap: wrap;
  font-size: var(--font-size-sm);
}

.footer-legal a {
  color: var(--color-gray-400);
  text-decoration: none;
  transition: color var(--transition-base);
}

.footer-legal a:hover {
  color: var(--color-white);
}

/* Badge Section */
.footer-badges {
  display: flex;
  justify-content: center;
  gap: var(--spacing-4);
  margin-top: var(--spacing-6);
  flex-wrap: wrap;
}

.footer-badge {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  padding: var(--spacing-2) var(--spacing-4);
  background-color: var(--color-gray-800);
  border-radius: var(--radius-md);
  font-size: var(--font-size-sm);
  color: var(--color-gray-300);
}

.footer-badge img {
  height: 20px;
  width: auto;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  footer {
    padding-top: var(--spacing-12);
    padding-bottom: var(--spacing-6);
  }

  .footer-content {
    padding: 0 var(--spacing-4);
    grid-template-columns: 1fr;
    gap: var(--spacing-6);
    text-align: center;
  }

  .footer-social {
    justify-content: center;
  }

  .footer-legal {
    flex-direction: column;
    gap: var(--spacing-2);
  }

  .footer-badges {
    flex-direction: column;
    align-items: center;
  }
}
