/* =========================================================================
   Love DNA Test Page Styles
   ========================================================================= */

/* CSS 변수 정의 */
:root {
  --love-bg: linear-gradient(135deg, #ffeaa7 0%, #fab1a0 100%);
  --love-primary: #ff6b9d;
  --love-secondary: #4ecdc4;
  --love-warning: #feca57;
  --love-text-primary: #2d3436;
  --love-text-secondary: #636e72;
  --love-text-light: #b2bec3;
  --love-gradient-primary: linear-gradient(135deg, #ff6b9d 0%, #ff8e9b 100%);
  --love-gradient-secondary: linear-gradient(135deg, #a8e6cf 0%, #4ecdc4 100%);
  --love-gradient-success: linear-gradient(135deg, #00b894 0%, #00a085 100%);
  --dna-gradient: linear-gradient(135deg, #ff6b9d 0%, #667eea 50%, #764ba2 100%);
}

/* 히어로 섹션 스타일 */
.love-hero {
  position: relative;
  min-height: 100vh;
  background: var(--love-bg);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 80px 20px;
  overflow: hidden;
}

.floating-hearts {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.floating-hearts .heart {
  position: absolute;
  font-size: 2rem;
  animation: float 6s ease-in-out infinite;
  opacity: 0.7;
}

.floating-hearts .heart:nth-child(1) {
  left: 10%;
  top: 20%;
  animation-delay: 0s;
}

.floating-hearts .heart:nth-child(2) {
  right: 15%;
  top: 30%;
  animation-delay: 1.5s;
}

.floating-hearts .heart:nth-child(3) {
  left: 20%;
  bottom: 30%;
  animation-delay: 3s;
}

.floating-hearts .heart:nth-child(4) {
  right: 10%;
  bottom: 20%;
  animation-delay: 4.5s;
}

.floating-hearts .heart:nth-child(5) {
  left: 50%;
  top: 10%;
  animation-delay: 2s;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0px) rotate(0deg);
    opacity: 0.7;
  }
  50% {
    transform: translateY(-20px) rotate(5deg);
    opacity: 1;
  }
}

.love-hero-content {
  max-width: 800px;
  text-align: center;
  position: relative;
  z-index: 2;
}

.love-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: rgba(255, 255, 255, 0.9);
  padding: 8px 20px;
  border-radius: 25px;
  font-size: 14px;
  font-weight: 600;
  color: var(--love-primary);
  margin-bottom: 30px;
  box-shadow: 0 4px 20px rgba(255, 107, 157, 0.2);
}

.love-hero h1 {
  font-size: 4rem;
  font-weight: 900;
  background: var(--dna-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 20px;
}

.love-hero p {
  font-size: 1.25rem;
  color: var(--love-text-secondary);
  margin-bottom: 40px;
  line-height: 1.6;
}

.love-stats {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-bottom: 40px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.love-stat {
  background: rgba(255, 255, 255, 0.9);
  padding: 20px;
  border-radius: 16px;
  text-align: center;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.love-stat-number {
  display: block;
  font-size: 2rem;
  font-weight: 900;
  color: var(--love-primary);
  margin-bottom: 8px;
}

.love-stat-label {
  font-size: 0.875rem;
  color: var(--love-text-secondary);
  font-weight: 600;
}

.love-cta {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  background: var(--love-gradient-primary);
  color: white;
  padding: 18px 40px;
  border-radius: 50px;
  font-size: 1.125rem;
  font-weight: 700;
  text-decoration: none;
  box-shadow: 0 8px 30px rgba(255, 107, 157, 0.4);
  transition: all 0.3s ease;
}

.love-cta:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 40px rgba(255, 107, 157, 0.6);
}

/* 특징 섹션 */
.love-features {
  padding: 100px 20px;
  background: #fff;
}

.love-section-title {
  font-size: 3rem;
  font-weight: 800;
  text-align: center;
  color: var(--love-text-primary);
  margin-bottom: 20px;
}

.love-section-subtitle {
  font-size: 1.25rem;
  text-align: center;
  color: var(--love-text-secondary);
  margin-bottom: 60px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.love-features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
}

.love-feature-card {
  background: #fff;
  padding: 40px 30px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.love-feature-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.love-feature-icon {
  font-size: 3rem;
  margin-bottom: 20px;
  display: block;
}

.love-feature-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--love-text-primary);
  margin-bottom: 16px;
}

.love-feature-desc {
  font-size: 1rem;
  color: var(--love-text-secondary);
  line-height: 1.6;
}

/* DNA 시스템 섹션 */
.love-dna-system {
  padding: 100px 20px;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
}

.love-dna-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 30px;
  max-width: 1000px;
  margin: 0 auto;
}

.love-dna-item {
  text-align: center;
  background: white;
  padding: 40px 20px;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.love-dna-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.love-dna-letter {
  font-size: 4rem;
  font-weight: 900;
  background: var(--dna-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 16px;
}

.love-dna-name {
  font-size: 1rem;
  font-weight: 700;
  color: var(--love-text-primary);
  margin-bottom: 12px;
}

.love-dna-desc {
  font-size: 0.875rem;
  color: var(--love-text-secondary);
  line-height: 1.5;
}

/* CTA 섹션 */
.love-cta-section {
  padding: 100px 20px;
  background: var(--love-bg);
  text-align: center;
}

.love-cta-title {
  font-size: 3rem;
  font-weight: 800;
  color: var(--love-text-primary);
  margin-bottom: 20px;
}

.love-cta-subtitle {
  font-size: 1.25rem;
  color: var(--love-text-secondary);
  margin-bottom: 50px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.love-cta-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.love-cta-primary {
  background: var(--love-gradient-primary);
  color: white;
  padding: 18px 40px;
  border-radius: 50px;
  font-size: 1.125rem;
  font-weight: 700;
  text-decoration: none;
  box-shadow: 0 8px 30px rgba(255, 107, 157, 0.4);
  transition: all 0.3s ease;
}

.love-cta-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 40px rgba(255, 107, 157, 0.6);
}

.love-cta-secondary {
  background: rgba(255, 255, 255, 0.9);
  color: var(--love-text-primary);
  padding: 18px 40px;
  border-radius: 50px;
  font-size: 1.125rem;
  font-weight: 700;
  text-decoration: none;
  border: 2px solid rgba(255, 107, 157, 0.3);
  transition: all 0.3s ease;
}

.love-cta-secondary:hover {
  background: white;
  border-color: var(--love-primary);
  color: var(--love-primary);
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(255, 107, 157, 0.2);
}

/* 광고 섹션 */
.ad-section {
  padding: 60px 20px;
  text-align: center;
  background: #f8f9fa;
}

.ad-label {
  font-size: 12px;
  color: #999;
  margin-bottom: 16px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* 전체 래퍼 */
.love-test-wrapper {
  min-height: 100vh;
  background: var(--love-bg);
  padding: 20px 0;
}

/* 헤더 섹션 */
.love-header-section {
  text-align: center;
  padding: 40px 20px;
  position: relative;
}

.love-header-decoration {
  font-size: 60px;
  position: absolute;
  opacity: 0.3;
  animation: float 6s ease-in-out infinite;
}

.love-header-decoration:first-child {
  left: 10%;
  top: 20%;
  animation-delay: 0s;
}

.love-header-decoration:last-child {
  right: 10%;
  top: 20%;
  animation-delay: 3s;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

.love-header-content {
  position: relative;
  z-index: 1;
}

.love-header-title {
  font-size: 42px;
  font-weight: 900;
  margin-bottom: 10px;
  background: var(--dna-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.love-header-subtitle {
  font-size: 18px;
  color: var(--love-text-secondary);
}

/* 테스트 컨테이너 */
.love-test-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
}

/* 인트로 섹션 */
.love-intro-section {
  background: white;
  border-radius: 24px;
  padding: 60px 40px;
  text-align: center;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
}

.love-intro-title {
  font-size: 32px;
  font-weight: 800;
  color: var(--love-text-primary);
  margin-bottom: 20px;
}

.love-intro-text {
  font-size: 18px;
  color: var(--love-text-secondary);
  line-height: 1.6;
  margin-bottom: 40px;
}

.love-start-button {
  background: var(--love-gradient-primary);
  color: white;
  border: none;
  padding: 16px 40px;
  border-radius: 50px;
  font-size: 18px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 30px rgba(255, 107, 157, 0.4);
}

.love-start-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 40px rgba(255, 107, 157, 0.6);
}

/* 테스트 화면 */
#test-screen {
  background: white;
  border-radius: 24px;
  padding: 40px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
}

/* 진행 상태 */
.love-progress-container {
  margin-bottom: 40px;
}

.love-progress-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.love-progress-text {
  font-size: 14px;
  color: var(--love-text-secondary);
  font-weight: 600;
}

.love-progress-bar {
  width: 100%;
  height: 8px;
  background: rgba(255, 107, 157, 0.2);
  border-radius: 10px;
  overflow: hidden;
}

.love-progress-fill {
  height: 100%;
  background: var(--love-gradient-primary);
  border-radius: 10px;
  transition: width 0.5s ease;
}

/* 질문 영역 */
.love-question-container {
  margin-bottom: 40px;
}

.love-question-card {
  text-align: center;
  margin-bottom: 30px;
}

.love-question-number {
  display: inline-block;
  background: var(--love-gradient-primary);
  color: white;
  padding: 6px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 20px;
}

.love-question-text {
  font-size: 24px;
  font-weight: 700;
  color: var(--love-text-primary);
  line-height: 1.4;
}

/* 옵션 영역 */
.love-options {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.love-option {
  background: rgba(255, 255, 255, 0.8);
  border: 2px solid #e9ecef;
  border-radius: 16px;
  padding: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 16px;
  font-weight: 500;
  color: var(--love-text-primary);
  text-align: left;
}

.love-option:hover {
  border-color: var(--love-primary);
  background: white;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 107, 157, 0.2);
}

.love-option.selected {
  border-color: var(--love-primary);
  background: linear-gradient(135deg, #fff 0%, #fff5f7 100%);
  box-shadow: 0 8px 25px rgba(255, 107, 157, 0.3);
}

/* 네비게이션 */
.love-navigation {
  display: flex;
  justify-content: space-between;
  gap: 16px;
}

.love-btn {
  padding: 14px 28px;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.love-btn-secondary {
  background: rgba(255, 255, 255, 0.9);
  color: var(--love-text-secondary);
  border: 2px solid var(--love-text-light);
}

.love-btn-secondary:hover:not(:disabled) {
  background: white;
  border-color: var(--love-primary);
  color: var(--love-primary);
  transform: translateY(-2px);
}

.love-btn-primary {
  background: var(--love-gradient-primary);
  color: white;
  box-shadow: 0 4px 15px rgba(255, 107, 157, 0.4);
}

.love-btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 107, 157, 0.6);
}

.love-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* 결과 화면 */
.love-result-container {
  background: white;
  border-radius: 24px;
  padding: 40px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
}

/* DNA 카드 */
.love-dna-card {
  text-align: center;
  padding: 40px;
  background: linear-gradient(135deg, #fff 0%, #f8f9fa 100%);
  border-radius: 20px;
  margin-bottom: 40px;
}

.love-result-dna {
  font-size: 60px;
  font-weight: 900;
  background: var(--dna-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 20px;
}

.love-result-title {
  font-size: 36px;
  font-weight: 800;
  color: var(--love-text-primary);
  margin-bottom: 10px;
}

.love-result-subtitle {
  font-size: 20px;
  color: var(--love-text-secondary);
  font-style: italic;
  margin-bottom: 20px;
}

.love-result-rarity {
  display: inline-block;
  background: var(--love-gradient-success);
  color: white;
  padding: 8px 20px;
  border-radius: 25px;
  font-size: 14px;
  font-weight: 600;
}

/* DNA 분석 */
.love-dna-analysis {
  background: #f8f9fa;
  border-radius: 20px;
  padding: 30px;
  margin-bottom: 40px;
}

.love-analysis-title {
  font-size: 24px;
  font-weight: 700;
  color: var(--love-text-primary);
  margin-bottom: 20px;
  text-align: center;
}

.love-dna-breakdown {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.love-axis-item {
  background: white;
  padding: 20px;
  border-radius: 12px;
  text-align: center;
  border-left: 4px solid var(--love-primary);
}

.love-axis-label {
  font-size: 14px;
  color: var(--love-text-secondary);
  font-weight: 600;
  display: block;
  margin-bottom: 8px;
}

.love-axis-code {
  font-size: 28px;
  font-weight: 900;
  color: var(--love-primary);
  display: block;
  margin-bottom: 4px;
}

.love-axis-description {
  font-size: 12px;
  color: var(--love-text-secondary);
}

/* 결과 분석 카드 */
.love-result-analysis {
  display: grid;
  gap: 20px;
}

.love-result-analysis-card {
  background: #f8f9fa;
  padding: 30px;
  border-radius: 16px;
  border-left: 4px solid var(--love-primary);
}

.love-result-analysis-card h4 {
  font-size: 20px;
  font-weight: 700;
  color: var(--love-text-primary);
  margin-bottom: 16px;
}

.love-result-analysis-card p {
  font-size: 16px;
  color: var(--love-text-secondary);
  line-height: 1.6;
}

/* 궁합 그리드 */
.love-compatibility-grid {
  display: grid;
  gap: 20px;
}

.love-compatibility-section h5 {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 12px;
}

.love-compatibility-best {
  color: var(--love-primary);
}

.love-compatibility-good {
  color: var(--love-secondary);
}

.love-compatibility-challenge {
  color: var(--love-warning);
}

.love-compatibility-types {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.love-compatibility-type,
.love-compatibility-type-good,
.love-compatibility-type-challenge {
  display: inline-block;
  padding: 6px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 500;
}

.love-compatibility-type {
  background: rgba(255, 107, 157, 0.1);
  color: var(--love-primary);
}

.love-compatibility-type-good {
  background: rgba(78, 205, 196, 0.1);
  color: var(--love-secondary);
}

.love-compatibility-type-challenge {
  background: rgba(254, 202, 87, 0.1);
  color: var(--love-warning);
}

/* 유명인 리스트 */
.love-celebrity-list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.love-celebrity-item {
  background: rgba(102, 126, 234, 0.1);
  color: #667eea;
  padding: 8px 20px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 500;
}

/* 공유 섹션 */
.love-share-section {
  text-align: center;
  margin-top: 40px;
}

.love-share-title {
  font-size: 24px;
  font-weight: 700;
  color: var(--love-text-primary);
  margin-bottom: 20px;
}

.love-share-buttons {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-bottom: 30px;
}

.love-share-btn {
  padding: 12px 24px;
  border: none;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.love-share-btn-kakao {
  background: #fee500;
  color: #000;
}

.love-share-btn-kakao:hover {
  background: #fdd835;
  transform: translateY(-2px);
}

.love-share-btn-copy {
  background: var(--love-gradient-secondary);
  color: white;
}

.love-share-btn-copy:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(168, 230, 207, 0.4);
}

.love-action-buttons {
  display: flex;
  justify-content: center;
  gap: 12px;
}

.love-action-buttons .love-btn {
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

/* 숨김 클래스 */
.love-hidden {
  display: none !important;
}

/* 반응형 디자인 */
@media (max-width: 1200px) {
  .love-features-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .love-dna-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 992px) {
  .love-hero h1 {
    font-size: 3rem;
  }

  .love-section-title {
    font-size: 2.5rem;
  }

  .love-cta-title {
    font-size: 2.5rem;
  }

  .love-dna-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .love-hero {
    min-height: 80vh;
    padding: 60px 20px;
  }

  .love-hero h1 {
    font-size: 2.5rem;
  }

  .love-hero p {
    font-size: 1.125rem;
  }

  .love-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }

  .love-features {
    padding: 80px 20px;
  }

  .love-section-title {
    font-size: 2rem;
  }

  .love-section-subtitle {
    font-size: 1.125rem;
  }

  .love-features-grid {
    grid-template-columns: 1fr;
    gap: 25px;
  }

  .love-feature-card {
    padding: 30px 25px;
  }

  .love-dna-system {
    padding: 80px 20px;
  }

  .love-dna-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .love-dna-item {
    padding: 30px 20px;
  }

  .love-dna-letter {
