/* =========================================================================
   Result Cards Component - Unified Design System
   ========================================================================= */

/* Base Result Container */
.result-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 var(--spacing-6);
  animation: resultFadeIn 0.6s ease-out;
}

/* Primary Result Card */
.result-card {
  background: var(--color-white);
  border-radius: var(--radius-2xl);
  overflow: hidden;
  margin-bottom: var(--spacing-8);
  box-shadow: var(--shadow-xl);
  position: relative;
}

/* Result Card Header */
.result-card-header {
  position: relative;
  padding: var(--spacing-10) var(--spacing-8);
  text-align: center;
  background: var(--result-gradient, linear-gradient(135deg, #818cf8 0%, #6366f1 100%));
  color: var(--color-white);
}

.result-card-header::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
  height: 60px;
  background: var(--color-white);
  clip-path: ellipse(100% 60px at 50% 100%);
}

/* Result Icon/Emoji */
.result-icon {
  font-size: 80px;
  display: block;
  margin-bottom: var(--spacing-4);
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.1));
  animation: bounce 1s ease-out;
}

/* Result Type Badge */
.result-type {
  display: inline-block;
  padding: var(--spacing-2) var(--spacing-4);
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border-radius: var(--radius-full);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-semibold);
  margin-bottom: var(--spacing-3);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* Result Title */
.result-title {
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-bold);
  margin-bottom: var(--spacing-2);
  line-height: 1.2;
}

/* Result Subtitle */
.result-subtitle {
  font-size: var(--font-size-lg);
  opacity: 0.9;
  font-style: italic;
}

/* Result Card Body */
.result-card-body {
  padding: var(--spacing-8);
  padding-top: var(--spacing-12);
}

/* Result Description */
.result-description {
  font-size: var(--font-size-lg);
  color: var(--color-gray-700);
  line-height: 1.8;
  text-align: center;
  margin-bottom: var(--spacing-8);
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

/* Result Sections Grid */
.result-sections {
  display: grid;
  gap: var(--spacing-6);
  margin-bottom: var(--spacing-8);
}

/* Result Section */
.result-section {
  background-color: var(--color-gray-50);
  border-radius: var(--radius-xl);
  padding: var(--spacing-6);
  border-left: 4px solid var(--section-color, var(--color-primary));
  transition: all var(--transition-base);
}

.result-section:hover {
  background-color: var(--color-gray-100);
  transform: translateX(4px);
  box-shadow: var(--shadow-md);
}

.result-section-title {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  color: var(--color-gray-900);
  margin-bottom: var(--spacing-3);
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
}

.result-section-content {
  color: var(--color-gray-700);
  line-height: 1.6;
}

/* Result Stats Grid */
.result-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: var(--spacing-4);
  margin-bottom: var(--spacing-8);
}

.result-stat {
  text-align: center;
  padding: var(--spacing-4);
  background-color: var(--color-gray-50);
  border-radius: var(--radius-lg);
  transition: all var(--transition-base);
}

.result-stat:hover {
  background-color: var(--color-primary-light);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.result-stat-value {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-bold);
  color: var(--color-primary);
  display: block;
  margin-bottom: var(--spacing-1);
}

.result-stat-label {
  font-size: var(--font-size-sm);
  color: var(--color-gray-600);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* Result Items (Tags, Pills) */
.result-items {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-2);
  justify-content: center;
  margin-bottom: var(--spacing-6);
}

.result-item {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-1);
  padding: var(--spacing-2) var(--spacing-4);
  background-color: var(--item-bg, var(--color-primary-light));
  color: var(--item-color, var(--color-primary-dark));
  border-radius: var(--radius-full);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  transition: all var(--transition-base);
}

.result-item:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-sm);
}

/* Result Chart Container */
.result-chart {
  margin: var(--spacing-8) 0;
  padding: var(--spacing-6);
  background-color: var(--color-gray-50);
  border-radius: var(--radius-xl);
  text-align: center;
}

/* Result Progress Bars */
.result-progress-list {
  display: grid;
  gap: var(--spacing-4);
  margin: var(--spacing-6) 0;
}

.result-progress-item {
  display: grid;
  grid-template-columns: 120px 1fr 60px;
  align-items: center;
  gap: var(--spacing-3);
}

.result-progress-label {
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  color: var(--color-gray-700);
  text-align: right;
}

.result-progress-bar {
  height: 8px;
  background-color: var(--color-gray-200);
  border-radius: var(--radius-full);
  overflow: hidden;
}

.result-progress-fill {
  height: 100%;
  background: var(
    --progress-gradient,
    linear-gradient(90deg, var(--color-primary) 0%, var(--color-primary-dark) 100%)
  );
  border-radius: var(--radius-full);
  transition: width 1s cubic-bezier(0.4, 0, 0.2, 1);
  width: var(--progress-value, 0%);
}

.result-progress-value {
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-semibold);
  color: var(--color-gray-900);
}

/* Result Advice Box */
.result-advice {
  background: linear-gradient(
    135deg,
    var(--color-primary-light) 0%,
    var(--color-secondary-light) 100%
  );
  border-radius: var(--radius-xl);
  padding: var(--spacing-8);
  margin: var(--spacing-8) 0;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.result-advice::before {
  content: '"✨"';
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 40px;
  background-color: var(--color-white);
  padding: 0 var(--spacing-4);
  border-radius: var(--radius-full);
}

.result-advice-text {
  font-size: var(--font-size-lg);
  color: var(--color-gray-800);
  font-style: italic;
  line-height: 1.8;
  margin-top: var(--spacing-4);
}

/* Share Actions */
.result-actions {
  display: flex;
  gap: var(--spacing-4);
  justify-content: center;
  flex-wrap: wrap;
  margin-top: var(--spacing-8);
  padding-top: var(--spacing-8);
  border-top: 1px solid var(--color-gray-200);
}

.result-action-btn {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-2);
  padding: var(--spacing-3) var(--spacing-6);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-medium);
  border-radius: var(--radius-full);
  border: none;
  cursor: pointer;
  transition: all var(--transition-base);
  text-decoration: none;
}

.result-action-primary {
  background-color: var(--color-primary);
  color: var(--color-white);
}

.result-action-primary:hover {
  background-color: var(--color-primary-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.result-action-secondary {
  background-color: var(--color-gray-200);
  color: var(--color-gray-700);
}

.result-action-secondary:hover {
  background-color: var(--color-gray-300);
}

.result-action-kakao {
  background-color: #fee500;
  color: #3c1e1e;
}

.result-action-kakao:hover {
  background-color: #fdd835;
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

/* Service-specific Color Schemes */

/* Fortune Services */
.fortune-result .result-card-header {
  --result-gradient: linear-gradient(135deg, #818cf8 0%, #6366f1 100%);
}

.fortune-daily .result-section {
  --section-color: #8b5cf6;
}
.fortune-saju .result-section {
  --section-color: #ec4899;
}
.fortune-tarot .result-section {
  --section-color: #3b82f6;
}
.fortune-zodiac .result-section {
  --section-color: #10b981;
}
.fortune-zodiac-animal .result-section {
  --section-color: #f59e0b;
}

/* Test Services */
.test-result .result-card-header {
  --result-gradient: linear-gradient(135deg, #f59e0b 0%, #dc2626 100%);
}

.test-teto .result-section {
  --section-color: #f59e0b;
}
.test-mbti .result-section {
  --section-color: #3b82f6;
}
.test-love .result-section {
  --section-color: #ec4899;
}

/* Tool Services */
.tool-result .result-card-header {
  --result-gradient: linear-gradient(135deg, #10b981 0%, #059669 100%);
}

.tool-text .result-section {
  --section-color: #10b981;
}
.tool-salary .result-section {
  --section-color: #8b5cf6;
}
.tool-bmi .result-section {
  --section-color: #3b82f6;
}

/* Animations */
@keyframes resultFadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .result-container {
    padding: 0 var(--spacing-4);
  }

  .result-card-header {
    padding: var(--spacing-8) var(--spacing-6);
  }

  .result-icon {
    font-size: 60px;
  }

  .result-title {
    font-size: var(--font-size-2xl);
  }

  .result-subtitle {
    font-size: var(--font-size-base);
  }

  .result-card-body {
    padding: var(--spacing-6);
    padding-top: var(--spacing-10);
  }

  .result-description {
    font-size: var(--font-size-base);
  }

  .result-stats {
    grid-template-columns: repeat(2, 1fr);
  }

  .result-progress-item {
    grid-template-columns: 1fr;
    gap: var(--spacing-2);
  }

  .result-progress-label {
    text-align: left;
  }

  .result-advice {
    padding: var(--spacing-6);
  }

  .result-actions {
    flex-direction: column;
  }

  .result-action-btn {
    width: 100%;
    justify-content: center;
  }
}
