/* 띠별 운세 페이지 전용 스타일 */

.zodiac-animal-section {
  padding: 60px 0;
  background: white;
}

.zodiac-date {
  text-align: center;
  margin-bottom: 40px;
}

.zodiac-date h2 {
  color: var(--fortune-primary);
  font-size: 1.8rem;
  margin-bottom: 10px;
}

.zodiac-date p {
  color: var(--gray-600);
  font-size: 1.1rem;
}

/* 반응형 스타일 */
@media (max-width: 768px) {
  .zodiac-date h2 {
    font-size: 1.5rem;
  }

  .zodiac-date p {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .zodiac-animal-section {
    padding: 40px 0;
  }

  .zodiac-date {
    margin-bottom: 30px;
  }

  .zodiac-date h2 {
    font-size: 1.3rem;
  }

  .zodiac-date p {
    font-size: 0.9rem;
  }
}
